import { css } from "styled-components";

/**
 *      .
 */
const CONTENT_DIRECTION = {
  /**
   *  
   * ```css
   * display: flex;
   * flex-direction: row;
   * ```
   */
  HORIZONTAL: css`
    display: flex;
    flex-direction: row;
  `,

  /**
   *  
   * ```css
   * display: flex;
   * flex-direction: column;
   * ```
   */
  VERTICAL: css`
    display: flex;
    flex-direction: column;
  `,
} as const;

export default CONTENT_DIRECTION;
